.sideMenuWrapper {
  min-width: 230px;
  background: #f3f4f8;
  padding: 20px 0;
  border-right: 1px solid #e8ebef;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sideMenuTitle {
  color: #768093;
  padding: 10px 25px;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  font-size: 12px;
}

.sideMenuItem {
  color: #768093;
  padding: 10px 25px;
  font-family: "Open Sans Normal", sans-serif;
  font-weight: 300;
  display: block;
  font-size: 14px;
  position: relative;
  cursor: pointer;
}

.sideMenuItemIcon {
  width: 25px;
  height: 25px;
  fill: currentColor;
  margin-right: 10px;
  vertical-align: middle;
}

.sideMenuIPinnedApps {
  border-top: 1px solid #e8ebef;
  border-bottom: 1px solid #e8ebef;
  background: #ecedf4;
  padding: 10px 0;
  margin: 10px 0;
}

.sideMenuItem:hover {
  color: #2f3148;
}

.selected.sideMenuItem .sideMenuItemIcon {
  fill: #058ed9;
}

.selected.sideMenuItem {
  color: #2f3148;
  background-color: #f9fafb;
}

.selected.sideMenuItem:after {
  content: "";
  border-top: 14px solid #ffffff;
  border-left: 14px solid #ffffff;
  transform: rotate(45deg);
  position: absolute;
  right: -8px;
  height: 0;
  width: 0;
  top: 16px;
}

.selected.sideMenuItem:before {
  content: "";
  border-top: 14px solid #e8ebef;
  border-left: 14px solid #e8ebef;
  transform: rotate(45deg);
  position: absolute;
  right: -7px;
  height: 0;
  width: 0;
  top: 16px;
}

