.cardWrapper {
  width: 100%;
  height: 100px;
  background: #f9fafb;
  border: 1px solid #e8ebef;
  border-radius: 4px;
  align-items: center;
  margin-bottom: 15px;
  position: relative;
  overflow: hidden;
  transition: all 300ms ease-in-out;
}

.cardWrapper:hover {
  transition: all 300ms ease-in-out;
  box-shadow: 0 5px 15px rgba(118, 128, 146, 0.1);
  border: 1px solid rgba(118, 128, 146, 0.32);
  transition: all 100ms ease-in-out;
  z-index: 1;
}

.cardWrapper:hover .expandStripeWrapper {
  bottom: -1px;
}

.expandStripe {
  box-shadow: 0px 2px 15px rgba(118, 128, 146, 0.2);
  cursor: pointer;
  text-align: center;
  width: 500px;
  background-color: #ecedf4;
  border-radius: 6px 6px 0 0;
  font-size: 12px;
  color: white;
  font-size: 10px;
  color: #768093;
  font-family: "Open Sans Bold", sans-serif;
}

.expandStripeWrapper {
  transition: all 150ms;
  transform: translateY(0px);
  bottom: -20px;
  height: 17px;
  line-height: 16px;
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
}

.cardWrapper.expanded {
  height: 300px;
}

.deviceTypeIcon {
  width: 25px;
  height: 25px;
}

.Section {
  padding: 0 25px;
  height: 50%;
  display: flex;
  align-items: center;
  border-right: 1px solid #e8ebef;
}

.IconSectionBackground {
  background-color: #ecedf4;
  height: 100%;
  z-index: 1;
}

.deviceContentWrapper {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: flex-start;
}

.subTitle {
  font-size: 10px;
  color: #768093;
  font-family: "Open Sans Bold", sans-serif;
}

.text {
  color: #2f3148;
  font-family: "Open Sans Normal", sans-serif;
  display: block;
  font-size: 14px;
}

.deviceType {
  width: 16%;
}

.deviceName {
  width: 28%;
}

.deviceSettingsMenu {
  width: 6.1%;
  padding: 0 25px;
  align-items: flex-end;
  z-index: 1;
}

.deviceSettingsMenuIcon {
  padding: 5px;
  transition: all 200ms ease-in-out;
  cursor: pointer;
  border: 1px solid transparent;
}

.deviceSettingsMenuIcon:hover {
  border: 1px solid rgba(118, 128, 146, 0.32);
}

.deviceStatus {
  width: 16%;
}

.cardSummary {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100px;
}

.nodeSettingPageWrapper {
  height: 66.5%;
  display: flex;
}

.placeHolderWidth {
  width: 78px;
}
.nodeSettingPage {
  width: 100%;
  margin: 0 25px;
  border-top: 1px solid #e8ebef;
}
