.contentHeader {
  background: rgba(5, 142, 217, 0.9);
  border-bottom: 1px solid #adadad;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.sectionTitle {
  font-size: 1.5em;
  padding: 0 25px;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  color: #fff;
}

.headerUserWrapper {
  padding-right: 30px;
  display: flex;
  align-items: center;
}

.headerUserThumbnail {
  background: #fff;
  height: 50px;
  width: 50px;
  border-radius: 3px;
  display: inline-block;
  margin-right: 15px;
  border: 3px solid rgba(5, 142, 217, 0.76);
}

.headerUserName {
  display: inline-block;
  font-family: "Open Sans Normal", sans-serif;
  position: relative;
  color: #fff;
  margin-right: 15px;
}

.headerOptions {
  width: 20px;
  height: 20px;
  background: rgba(249, 250, 251, 0.3);
  border-radius: 50%;
}

.headerTriangle {
  border-top: 5px solid #fff;
  height: 0;
  width: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  margin: auto;
  top: 8px;
  position: relative;
}
